@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
:root {
    --papaya: #FAB41E;
    --yaleBlue: #17468B;
    --avsBurgandy: #6F263D;
    --avsBlue: #236192;
    --avsSilver: #A2AAAD;
}

*, *::before, *::after {
    font-family: "Roboto", sans-serif;
    box-sizing: border-box;
    padding: 10px;
}

html {
    font-size: 16px;
    background-color: #A2AAAD;
    background-color: var(--avsSilver);
}

h1 {
    font-family: "Roboto Slab", serif;
    font-weight: 700;
    font-size: 26px;
    color: white;
}

h2, h3, h4, h5, h6 {
    font-weight: 500;
    font-size: 20px;
    color: white;
}

p, li, ol, ul {
    font-weight: 400;
    font-size: 16px;
    line-height: 1.6em;
}
body {
  font-family: Arial;
  padding: 20px;
  background: #f1f1f1;
}

/* Header/Blog Title */
#blogHeader {
  padding: 30px;
  font-size: 40px;
  text-align: center;
  background: white;
}

#blogMain {
  width: 100%;
  height: 100%;
}

/* Create two unequal columns that floats next to each other */
/* Left column */
#blogLeftColumn {
  float: left;
  width: 74%;
}

/* Right column */
#blogRightColumn {
  float: right;
  width: 24%;
  padding-left: 20px;
}

/* Fake image */
.blogFakeImg {
  background-color: #aaa;
  width: auto;
  height: 200px;
  padding: 0px;
  display: flex;
  justify-content: center;
}

.blogFakeImg img {
  height: 100%;
}

/* Add a card effect for articles */
.blogCard {
  background-color: white;
  padding: 15px;
  margin-top: 20px;
}

#blogHeader h2, .blogCard h3, .blogCard h4 {
  color: black;
}

/* Clear floats after the columns */
.row:after {
  content: "";
  display: table;
  clear: both;
}

/* Footer */
footer {
  padding: 20px;
  text-align: center;
  background: #ddd;
  margin-top: 20px;
}

#follower img {
  height: 50px;
  width: 50px;

}
/* Responsive layout - when the screen is less than 800px wide, make the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 800px) {
  .leftcolumn, .rightcolumn {
      width: 98%;
      padding: 0;
  }
}
