.thumbnail {
  position: relative;
  zoom: normal;
  transform: none;
  transform-origin: 0 0;
  height: 100%;
}

.thumbnail img {
  height: 100%;
  border: black solid 1px;
  padding: 5px;
}

.thumbnail:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.thumbnail-container {
  width: calc(1440px * 0.25);
  height: calc(900px * 0.3);
  display: flex;
  justify-content: center;
  overflow: hidden;
  position: relative;
}

