  }

@keyframes showTab {
    from {
        opacity:0;
        transform:translateY(10px)
    } to {
        opacity:1;
        transform:translateY(0)
    }
}

.tabsy > button {
    width:100%;
    font-family:inherit;
    font-size:100%;
    margin:0;
    outline:0;
    border:0;
    vertical-align:baseline;
    box-sizing:border-box;
    background:transparent
}

.tabsy > .tabButton {
    display:block;
    margin-top:1px;
    background:#5a5243;
    color:#f5f5f5;
    text-align:center;
    transition:all 250ms ease-in-out
}

.tabsy>input { 
    display:none
}

.tabsy > input:checked+label+.tab {
    display:block
}

.tabsy > input:checked+label+.tab>.content {
    animation:showTab 250ms ease-in-out
} 
.tabsy>.tab {
    background:#fff;
    display:none
}

@media screen and (min-width: 768px) {
    .tabsy {
        display:flex;
        flex-wrap:wrap
    }
    
    .tabsy>.tabButton {
        order:1;
        align-items:flex-start;
        cursor:pointer
    }
    
    .tabsy>input:checked+label {
        background:#fff;
        color:#5a5243
    } 
    
    .tabsy>.tab {
        order:2
    }

    .thumbnail img {
      width: 100%;
      height: auto;
    }
}

@media screen and (max-width: 750px) {
  .thumbnail img {
    width: 100%;
    height: auto;
  }
}
